@import './../../styles/index.scss';

.signupContainer {
  margin-bottom: 12px;
}

.navContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 13px;
}

.searchContainer {
  display: flex;
  gap: 20px;
  align-items: center;
}

.icons {
  display: flex;
  gap: 20px;
}

.label {
  display: block;
  position: relative;
  width: 282px;
  height: 100%;
}

.search {
  width: 282px;
  height: 40px;
  background-color: rgba($c-black, 0.1);
  border: none;
  border-radius: 15px;
}

.iconAbsolute {
  position: absolute;
  left: 9px;
  top: 50%;
  transform: translateY(-50%);
}

.icon,
.iconAbsolute {
  width: 21px;
  height: 21px;
}
