@import './../../styles/index.scss';

.contacts {
  padding-top: 46px;
  margin-bottom: 52px;
}

.line {
  width: 100%;
  height: 1px;
  background-color: rgba($c-black, 0.2);
  margin-bottom: 46px;
}

.container {
display: grid;
grid-template-columns: 2fr 1fr 1fr 1fr;
}

.title {
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
  color: rgba($c-black, 0.8);
  margin-bottom: 30px;
}

.title::first-letter {
  font-size: 18px;
}

.item {
font-size: 16px;
color: rgba($c-black, 0.6);
text-transform: uppercase;
margin-bottom: 20px;
}

.item::first-letter {
  font-size: 18px;
}

