@import './index.scss';

// ! Banner slider
.bannerSlider .swiper-button-next,
.swiper-button-prev {
  color: $c-white !important;
  width: 29px;
  border-radius: 5px;
}

.bannerSlider .swiper-button-next {
  padding-right: 50px;
}

.bannerSlider .swiper-button-prev {
  padding-left: 50px;
}

.bannerSlider .swiper-pagination-bullet {
  background-color: rgba($c-white, 0.6) !important;
}

.bannerSlider .swiper-pagination-bullet-active {
  background-color: $c-white !important;
}

// ! Goods slider
.goodsSlider {
  position: relative;
  height: 640px;
  overflow: visible;
}

.goodsSlider .swiper-wrapper {
  padding-top: 60px;
}

.goodsSlider .swiper-slide {
  width: 100%;
  max-width: 370px;
  margin-right: 30px;
}

.goodsSlider .swiper-button-prev {
  top: 0;
  left: 92%;
}

.goodsSlider .swiper-button-next {
  top: 0;
  right: 17px;
}

.goodsSlider .swiper-button-next::after,
.goodsSlider .swiper-button-prev::after {
  content: '';
}

.goodsSlider .swiper-button-prev::before {
  content: url('./../assets/svg/arrow-left.svg');
  width: 25px;
  height: 10px;
}

.goodsSlider .swiper-button-next::before {
  content: url('./../assets/svg/arrow-right.svg');
  width: 25px;
  height: 10px;
}

.goodsSlider .swiper-pagination {
  width: 50px;
  top: 0;
  left: 85%;
}

// ! Reviews slider
.reviewsSlider {
  position: relative;
  width: 100%;
  height: 310px;
}

.reviewsSlider .swiper-wrapper {
  padding-top: 50px;
}

.reviewsSlider .swiper-slide {
  width: 100%;
  margin-right: 30px !important;
}

.reviewsSlider .swiper-button-prev {
  top: 0;
  left: 92%;
}

.reviewsSlider .swiper-button-next {
  top: 0;
  right: 17px;
}

.reviewsSlider .swiper-button-next::after,
.reviewsSlider .swiper-button-prev::after {
  content: '';
}

.reviewsSlider .swiper-button-prev::before {
  content: url('./../assets/svg/arrow-left.svg');
  width: 25px;
  height: 10px;
}

.reviewsSlider .swiper-button-next::before {
  content: url('./../assets/svg/arrow-right.svg');
  width: 25px;
  height: 10px;
}

.reviewsSlider .swiper-pagination {
  width: 50px;
  top: 0;
  left: 85%;
}
