@import './../../styles/index.scss';

.reviewsSlider {
  margin-bottom: 157px;
  padding-top: 10px;
}

.title {
  position: absolute;
  font-weight: $fw-medium;
  font-size: 26px;
  color: rgba($c-black, 0.8);
  top: -5px;
}

.title::first-letter {
  font-size: 28px;
}

.swiperSlide {
  width: 100%;
  max-width: 270px;
  height: 250px;
  padding: 30px 37px;
  border: 1px solid $c-black;
  border-radius: 10px;
}

.contentContainer {
  display: flex;
  align-items: center;
  gap: 19px;
  margin-bottom: 15px;
}

.imageContainer {
  width: 100%;
  max-width: 50px;
}

.iconsContainer {
  display: flex;
  align-items: center;
  gap: 2px;
}

.name {
  font-weight: $fw-medium;
  line-height: 30px;
  color: $c-black;
  font-size: 14px;
  text-transform: uppercase;
}

.name::first-letter {
  font-size: 16px;
}

.icon {
  width: 12px;
  height: 12px;
}

.description {
  line-height: 24px;
}

