* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 0;
}

img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}

body {
  font-family: $ff-main;
  font-weight: $fw-regular;
  font-size: 16px;
  color: rgba($c-black, 0.6);
}

a {
  text-decoration: none;
  color: $c-black;
  cursor: pointer;
}

svg {
  width: 100%;
  height: 100%;
}

.container {
  height: 100%; // допомагає, коли колір бекграунду не покриває весь контент внизу
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  // border: 1px solid red;
}

.padding {
  padding-left: 98px;
  padding-right: 98px;
}


.icon-arrow-left {
  width: 25px;
  height: 1px;
}