@import './../../styles/index.scss';

.signup {
  background-color: rgba($c-black, 0.8);
  padding-top: 16px;
  padding-bottom: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: $c-white;
}

.number, .text {
  cursor: pointer;
}

.text {
  font-size: 14px;
  text-transform: uppercase;

}

.text::first-letter {
  font-size: 16px;
}

.numberContainer {
  display: flex;
  align-items: center;
  gap: 23px;
}

.link {
  color: $c-white;
}