@import './../../styles/index.scss';

.shares {
  padding-top: 90px;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 610px;
  text-align: center;
}

.title {
  font-weight: $fw-medium;
  font-size: 38px;
  color: rgba($c-black, 0.8);
  margin-bottom: 30px;
  text-transform: uppercase;
}

.title::first-letter {
  font-size: 40px;
}

.description {
  color: rgba($c-black, 0.8);
  text-transform: uppercase;
  margin-bottom: 40px;
  line-height: 24px;
}

.description::first-letter {
  font-size: 18px;
}

.descrition::first-letter {
  font-size: 18px;
}

.subscribeContainer {
  margin: 0 auto;
  width: 100%;
  max-width: 570px;
  position: relative;
  margin-bottom: 108px;
}

.input {
  color: rgba($c-black, 0.4);
  width: 100%;
  height: 50px;
  padding-left: 18px;
  border-radius: 15px;
  border: 1px solid $c-blur;
}

.btn {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $c-blur;
  height: 50px;
  padding: 20px 35px;
  color: $c-white;
  border: none;
  border-radius: 15px;
  text-transform: uppercase;
}

.btn::first-letter {
  font-size: 18px;
}
