@import './../../styles/index.scss';

.gifts {
  display: flex;
  align-items: center;
  gap: 30px;
  padding-top: 36px;
  padding-bottom: 80px;
}

.title {
  font-weight: $fw-medium;
  font-size: 34px;
  text-transform: uppercase;
  color: rgba($c-black, 0.8);
  margin-bottom: 30px;
  line-height: 50px;
}

.title::first-letter {
  font-size: 38px;
}

.description {
  width: 100%;
  max-width: 450px;
  color: rgba($c-black, 0.6);
  text-transform: uppercase;
  margin-bottom: 40px;
  line-height: 24px;
}

.description::first-letter {
  font-size: 18px;
}

.bigLetter {
  font-size: 18px;
}

.btn {
  color: $c-white;
  background-color: $c-black;
  padding: 20px 35px;
  text-transform: uppercase;
  border-radius: 15px;
  cursor: pointer;
}

.btn::first-letter {
  font-size: 18px;
}

.imageContainer {
  width: 100%;
  max-width: 570px;
}
