@import './../../styles/index.scss';

.banner {
  width: 100%;
  height: 100%;
}

.bannerSlider {
  width: 100%;
  height: 490px;
}

.swiperSlide {
  width: 100%;
  height: 100%;

  background-image: url('./../../assets/bg.png');
}

.content {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 130px;
}

.titleContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  max-width: 470px;
  height: 100%;
}

.title {
  margin-top: 0;
  margin-bottom: 0;
  font-family: $ff-banner;

  font-size: 64px;
  color: $c-white;
  font-weight: $fw-black;
}

.subtitle {
  font-family: $ff-banner;
  font-size: 64px;
  color: $c-white;
  font-weight: $fw-light;
}

.discount {
  display: flex;
  justify-content: center;
  align-items: center;
  color: $c-white;
  font-family: $ff-banner;
  width: 370px;
  height: 60px;
  background-color: rgba($c-white, 0.3);
  backdrop-filter: blur(10px);
  font-size: 40px;
  font-weight: $fw-light;
  margin-bottom: 20px;
}

.description {
  text-align: center;
  font-family: $ff-banner;
  color: $c-white;
  font-weight: $fw-regular;
  font-size: 20px;
}
