@import './../../styles/index.scss';

.title {
  position: absolute;
  font-weight: $fw-medium;
  font-size: 26px;
  color: rgba($c-black, 0.8);
  top: -5px;
}

.imageContainer {
  position: relative;
  margin-bottom: 15px;
  width: 100%;
  max-width: 370px;
}

.iconContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: $c-white;
  width: 35px;
  height: 35px;
  border-radius: 50%;
}

.icon {
  width: 17px;
  height: 15px;
}

.contentTitle {
  font-weight: $fw-medium;
  font-size: 20px;
  color: rgba($c-black, 0.8);
  text-transform: uppercase;
  margin-bottom: 20px;
}

.contentTitle::first-letter {
  font-size: 22px;
}

.type,
.description {
  text-transform: uppercase;
}

.type {
  margin-bottom: 10px;
}

.type::first-letter {
  font-size: 18px;
}

.description {
  margin-bottom: 17px;
}

.price {
  font-weight: $fw-bold;
  color: $c-price;
  text-transform: uppercase;
}

.goodsSlider {
  margin-bottom: 80px;
}

.line {
  width: 100%;
  height: 1px;
  background-color: rgba($c-black, 0.4);
}
